export default {
	// shaderCPUGPU2d
	// shadercpu
	// shader
	vertexShader: `
	precision mediump float;
	attribute vec2 position;
	attribute vec4 color;
	uniform mat3 transform;
	uniform float z;
	varying vec4 vColor;
	void main()
	{
		vColor = color;
		vec3 mPosition = transform * vec3(position,1.);
		gl_Position = vec4(mPosition.xy,z,1.);
	}
	`,

	fragmentShader: `
	precision mediump float;
	varying vec4 vColor;
	void main()
	{
		gl_FragColor = vColor;
	}
	`
}